#content{display:flex;flex-direction:column;align-items:center;text-align:center;padding:40px 0}#content h1{font-size:1.5rem;margin-bottom:20px;position:relative;font-weight:bold}#content h1::after{content:"";display:block;width:50px;height:3px;background-color:#56397b;margin:10px auto 0}#content .imageWrapper{position:relative}#content .circle{position:absolute;left:50%;transform:translateX(-50%);z-index:-1;height:360px;width:300px;padding-top:20px}#content .student{position:absolute;top:0;left:50%;transform:translateX(-50%);z-index:2}#content .content{padding-top:50px}#content .button{background-color:#56397b;color:#fff;padding:16px 50px;border-radius:5px;border:none;font-size:1rem;cursor:pointer;transition:background-color .3s;margin-top:350px;z-index:3}#content .button:hover{background-color:#4a2f6d}