.GalleryMainContent{padding-top:50px}.GalleryMainContent .content .galleryHead{display:flex;justify-content:center;flex-direction:column;gap:6px;align-items:center}.GalleryMainContent .content .galleryHead h1{font-size:28px;font-weight:600;line-height:33.6px;color:#333}.GalleryMainContent .content .galleryHead hr{width:40px;height:2px;border:none;border-radius:12px;background:#6a00a1}.GalleryMainContent .content .gallery_card{margin-top:42px;display:grid;grid-template-columns:1fr 1fr 1fr;gap:24px}.GalleryMainContent .content .gallery_card .galleryItem img{cursor:pointer;width:380px;height:258px;border-radius:10px}.GalleryMainContent .content .gallery_card .galleryItem img:hover{transform:scale(1.01);box-shadow:0 10px 16px rgba(0,0,0,.2);transition:transform .3s ease}.GalleryMainContent .content .buttonContainer{display:flex;justify-content:center;margin-top:20px}.GalleryMainContent .content .button{width:100%;max-width:180px;padding:16px;background:#6a00a1;border-radius:16px;color:#fff;font-weight:400;font-size:16px;cursor:pointer;border:none;text-align:center}.GalleryMainContent .content .button:hover{transform:scale(1.01);transition:transform .3s ease}@media(max-width: 430px){.GalleryMainContent .gallery_card{overflow-x:auto;grid-template-columns:repeat(6, 1fr) !important}}